// app/api/visit/route.ts
import { NextResponse } from 'next/server';
import fs from 'fs';
import path from 'path';

const VISIT_FILE = path.resolve(process.cwd(), 'visits.json');

export async function GET() {
  let count = 0;
  try {
    const data = fs.readFileSync(VISIT_FILE, 'utf-8');
    count = JSON.parse(data).count || 0;
  } catch {
    count = 0;
  }
  return NextResponse.json({ count });
}

export async function POST() {
  let count = 0;
  try {
    const data = fs.readFileSync(VISIT_FILE, 'utf-8');
    count = JSON.parse(data).count || 0;
  } catch {
    count = 0;
  }

  count += 1;
  fs.writeFileSync(VISIT_FILE, JSON.stringify({ count }), 'utf-8');
  return NextResponse.json({ count });
}
